import React, { useState, useEffect } from 'react';
import { Box, Button, Typography, RadioGroup, FormControlLabel, Radio } from '@mui/material';
import QuizQuestion from './QuizQuestion';

const Quiz = ({ characters, updateStatistics }) => {
  const [quizSize, setQuizSize] = useState(null);
  const [score, setScore] = useState(0);
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [quizCompleted, setQuizCompleted] = useState(false);
  const [answers, setAnswers] = useState([]);
  const [selectedCharacters, setSelectedCharacters] = useState([]);
  const [quizStarted, setQuizStarted] = useState(false);

  useEffect(() => {
    if (quizSize !== null && selectedCharacters.length === 0) {
      const shuffledCharacters = [...characters].sort(() => 0.5 - Math.random());
      const selected = quizSize === 'all' ? shuffledCharacters : shuffledCharacters.slice(0, quizSize);
      setSelectedCharacters(selected);
      setScore(0);
      setCurrentQuestion(0);
      setQuizCompleted(false);
      setAnswers([]);
    }
  }, [quizSize, characters, selectedCharacters.length]);

  const handleAnswer = (isCorrect, question, answer) => {
    setAnswers(prevAnswers => [...prevAnswers, { question, answer, correct: isCorrect }]);
    if (isCorrect) {
      setScore(prevScore => prevScore + 1);
    }
    const nextQuestion = currentQuestion + 1;
    if (nextQuestion < selectedCharacters.length) {
      setCurrentQuestion(nextQuestion);
    } else {
      setQuizCompleted(true);
      updateStatistics(score + (isCorrect ? 1 : 0));
    }
  };

  const startQuiz = () => {
    setQuizStarted(true);
  };

  const resetQuiz = () => {
    setQuizSize(null);
    setSelectedCharacters([]);
    setScore(0);
    setCurrentQuestion(0);
    setQuizCompleted(false);
    setAnswers([]);
    setQuizStarted(false);
    localStorage.removeItem('quizSize');
    localStorage.removeItem('quizScore');
    localStorage.removeItem('currentQuestion');
    localStorage.removeItem('quizAnswers');
    localStorage.removeItem('selectedCharacters');
    localStorage.removeItem('quizStarted');
  };

  if (!quizStarted) {
    return (
      <Box textAlign="center" my={4}>
        <Typography variant="h4" gutterBottom>
          Choose Quiz Size
        </Typography>
        <RadioGroup row value={quizSize} onChange={(e) => setQuizSize(e.target.value)}>
          <FormControlLabel value="5" control={<Radio />} label="5 Huruf" />
          <FormControlLabel value="10" control={<Radio />} label="10 Huruf" />
          <FormControlLabel value="all" control={<Radio />} label="Semua Huruf" />
        </RadioGroup>
        <Button variant="contained" color="secondary" onClick={startQuiz} disabled={!quizSize} style={{ marginTop: 16 }}>
          Start Quiz
        </Button>
      </Box>
    );
  }

  if (selectedCharacters.length === 0) {
    return <div>Loading...</div>;
  }

  const currentChar = selectedCharacters[currentQuestion];
  const options = [...characters]
    .sort(() => 0.5 - Math.random())
    .slice(0, 3)
    .map(char => char.romaji);
  if (!options.includes(currentChar.romaji)) {
    options[Math.floor(Math.random() * options.length)] = currentChar.romaji;
  }

  return (
    <div>
      {!quizCompleted ? (
        <QuizQuestion
          question={`What is the romaji for ${currentChar.character}?`}
          options={options}
          correctAnswer={currentChar.romaji}
          onAnswer={(isCorrect, question, answer) => handleAnswer(isCorrect, question, answer)}
        />
      ) : (
        <div className="quiz-results">
          <h2>Quiz Completed!</h2>
          <p>Your Score: {score}/{selectedCharacters.length}</p>
          <div className="answers-summary">
            <h3>Summary</h3>
            <ul>
              {answers.map((answer, index) => (
                <li key={index} className={answer.correct ? 'correct' : 'incorrect'}>
                  {answer.question} - Your answer: {answer.answer} - {answer.correct ? 'Correct' : 'Incorrect'}
                </li>
              ))}
            </ul>
          </div>
          <Button variant="contained" color="primary" onClick={resetQuiz}>
            Try Again
          </Button>
        </div>
      )}
    </div>
  );
};

export default Quiz;
