import React from 'react';
import { Box, Typography } from '@mui/material';

const Statistics = ({ totalQuizzes, averageScore }) => {
  return (
    <Box textAlign="center" my={4}>
      <Typography variant="h4">Statistics</Typography>
      <Typography variant="h6">Total Quizzes: {totalQuizzes}</Typography>
      <Typography variant="h6">Average Score: {averageScore.toFixed(2)}</Typography>
    </Box>
  );
};

export default Statistics;
