import React, { useState } from 'react';
import { Box, Button, Typography } from '@mui/material';

const correctSound = new Audio('/sounds/correct.mp3');
const incorrectSound = new Audio('/sounds/incorrect.mp3');

const QuizQuestion = ({ question, options, correctAnswer, onAnswer }) => {
  const [selectedOption, setSelectedOption] = useState(null);

  const handleOptionClick = (option) => {
    const isCorrect = option === correctAnswer;
    setSelectedOption(option);
    if (isCorrect) {
      correctSound.play();
    } else {
      incorrectSound.play();
    }
    setTimeout(() => {
      onAnswer(isCorrect, question, option);
      setSelectedOption(null);
    }, 1000);
  };

  return (
    <Box textAlign="center" my={4}>
      <Typography variant="h4">{question}</Typography>
      <Box my={2}>
        {options.map((option, index) => (
          <Button
            key={index}
            variant="contained"
            color={selectedOption === option ? (option === correctAnswer ? 'success' : 'error') : 'primary'}
            onClick={() => handleOptionClick(option)}
            style={{ margin: '0 10px', minWidth: '100px' }}
            disabled={selectedOption !== null}
          >
            {option}
          </Button>
        ))}
      </Box>
      {selectedOption && (
        <Typography variant="h6" color={selectedOption === correctAnswer ? 'success.main' : 'error.main'}>
          {selectedOption === correctAnswer ? 'Correct!' : `Incorrect! The correct answer is ${correctAnswer}`}
        </Typography>
      )}
    </Box>
  );
};

export default QuizQuestion;
